import React from 'react';

import 'bootstrap/dist/css/bootstrap.css'
import './ParrotStage.scss';

import BootstrapSwitchButton from 'bootstrap-switch-button-react'
import axios from 'axios';
import {NotificationManager} from 'react-notifications';
import {baseUrl, parrotCommandsAPI, performParrotCommandAPI} from '../../../../../APIData';
import {CountDown} from '../../../../CountDown';
import SwitchOptionWrapper from '../SwitchOptionWrapper';

export default class ParrotStage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            autoMode: null,
            parrotAutoMode: null,
        }
        this.movementCommands = [];
        this.voiceCommands = [];
    }
    
    componentDidMount() {
        axios.get(baseUrl + parrotCommandsAPI, {
            headers: {
                token: localStorage.getItem("token"),
            }
        })
        .then(res => {
            this.movementCommands = res.data.movement;
            this.voiceCommands = res.data.voice;
        })
        .catch(error => {
            if (typeof error.response == "undefined"){
                ((message) => {NotificationManager.error(message);})('ارتباط با سرور برقرار نشد');
                return;
            }
            ((message) => {NotificationManager.error(message);})(error.response.data.detail);
        });

    }


    sendParrotCommand = (command) => {
        if (this.state.parrotAutoMode)
            return;
        axios.post(baseUrl + performParrotCommandAPI, 
            {commandID: command.id}, 
            {headers: {token: localStorage.getItem("token")}}
        )
        .then(res => {
            ((message) => {NotificationManager.success(message);})('دستور ارسال شد');
        })
        .catch(error => {
            console.log(error.request);
            if (typeof error.response == "undefined"){
                ((message) => {NotificationManager.error(message);})('ارتباط با سرور برقرار نشد');
                return;
            }
            ((message) => {NotificationManager.error(message);})(error.response.data.detail);
        });
    }

    renderCommands(list) {
        let commands = []
        for (const item of list) {
            commands.push(
                <div className= "button-surrounding">
                    <button className= "command-btn" onClick= {() => {this.sendParrotCommand(item)}}>{item.title}</button>
                </div>
            );
        }
        return commands;
    }

    render() {
        
        return (
            <div id= "parrot-stage" className= "d-flex flex-column align-items-center mt-2">

                <div id= "timer" className= "mt-5">
                    <CountDown key= {String(!this.props.isCurrentStage || !this.state.autoMode) + String(this.props.duration)} 
                        duration= {this.props.duration} 
                        halt= {!this.props.isCurrentStage || (this.state.autoMode == false)}
                        notifyCountEnd= {this.props.notifyEnd}/>
                </div>

                <SwitchOptionWrapper title= "تغییر مرحله">
                        <BootstrapSwitchButton
                            checked={true}
                            onlabel='خودکار'
                            onstyle='success'
                            offlabel='دستی'
                            offstyle='info'
                            style='w-100 mx-3'
                            onChange={(checked) => {
                                this.setState({autoMode: checked});
                            }}
                        />
                </SwitchOptionWrapper>
                
                <SwitchOptionWrapper title= "دستورات طوطی">
                        <BootstrapSwitchButton
                            checked={true}
                            onlabel='خودکار'
                            onstyle='success'
                            offlabel='دستی'
                            offstyle='info'
                            style='w-100 mx-3'
                            onChange={(checked) => {
                                this.setState({parrotAutoMode: checked});
                            }}
                        />
                </SwitchOptionWrapper>
                
                <div id= "control-box" className= {`row flex-row justify-content-center align-items-stretch mt-4
                                    ${this.state.parrotAutoMode == null ? " invisible" : this.state.parrotAutoMode ? " hidden" : " visible"}`}>
                    <div className= "col-md-6 col-10 align-items-center temp no-left-border rounded pt-2">
                        <div>دستورات حرکتی</div>
                        <div className= "command-box mt-3 d-flex flex-row justify-content-center flex-wrap ">
                            {this.renderCommands(this.movementCommands)}
                        </div>
                    </div>
                    <div className= "col-md-6 col-10 flex-column align-items-center temp rounded pt-2">
                        <div>دستورات صوتی</div>
                        <div className= "command-box mt-3 d-flex flex-row justify-content-center flex-wrap">
                            {this.renderCommands(this.voiceCommands)}
                        </div>
                    </div>
                </div>
                <div className= "mt-4">
                    <button className= {`button btn btn-primary ${this.state.autoMode == null ? " invisible" :
                        this.state.autoMode ? " hidden" : " visible"}`} onClick= {this.props.notifyEnd}>اتمام تست</button>
                </div>
            </div>
        );
    }
}