import React from 'react';
import axios from 'axios';
import {NotificationManager} from 'react-notifications';
import {EditInfo} from './EditInfo';
import {baseUrl, getUserAPI} from '../../APIData';
import 'bootstrap/dist/css/bootstrap.css'
import './PatientInfo.scss';


import girl from '../../assets/images/icons/girl.png';
import boy from '../../assets/images/icons/boy.png';
import calendar from '../../assets/images/icons/calendar.png';
import phone from '../../assets/images/icons/phone.png';
import ID from '../../assets/images/icons/ID.png';
import doctor from '../../assets/images/icons/doctor.png';

import {en2fa} from '../../utils';

export default class PatientInfo extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            person: null,
            loading: true,
            editInfoOpen: false,
        }
    }
    
    componentDidMount() {
        this.fetchUserData();
    }
    fetchUserData = () => {
        this.setState({loading: true}, () => {
            axios.get(baseUrl + getUserAPI, {
                headers: {
                    token: localStorage.getItem("token"),
                }
            })
            .then(res => {
                this.setState({person: res.data, loading: false});
            })
            .catch(error => {
                if (typeof error.response == "undefined") {
                    ((message) => {NotificationManager.error(message);})('ارتباط با سرور برقرار نشد');
                }
                else if (String(error.response.status)[0] == 5)
                    ((message) => {NotificationManager.error(message);})('خطای پیش‌بینی نشده‌ای در سمت سرور رخ داده.');
                else ((message) => {NotificationManager.error(message);})(error.response.data.detail);
            });
        })
    }

    render() {
        return this.state.loading ? null : (
                <div id= "patient-info" className= {`d-felx flex-column justify-content-center align-items-center`}>
                    <div id="tile" className= "d-flex flex-column align-items-center justify-content-around">
                        {this.state.person.gender == 'female' 
                            ? <img src={girl}/>
                            : <img src={boy}/>
                        }
                        <p style= {{direction: "rtl"}}>‬
                            <span>{this.state.person.first_name + ' ' + this.state.person.last_name} </span>
                            <span id= "logout" className= "mr-1" style= {{"font-size": "12px"}}>
                                (<a id= "logout-link" onClick= {this.props.onLogout}>خروج</a>)
                            </span>
                        </p>
                    </div>
                    <div id= "info-body" className= "d-flex flex-column">
                        <div className="detail-item flex-sm-row flex-column flex-nowrap" id="age">
                            <img className="detail-icon" title= "بازه سنی" src={calendar}/>
                            <div className= "ml-sm-3">{ this.a = ((new Date()).getFullYear() - (this.state.person.birth_year) - 622)}-{this.a +1}</div>
                        </div>
                        <div className="detail-item flex-sm-row flex-column flex-nowrap" id="phone_number">
                            <img className="detail-icon" title= "تلفن" src={phone}/>
                            <div className= "ml-sm-3">{this.state.person.phone_number}</div>
                        </div>
                        <div className="detail-item flex-sm-row flex-column flex-nowrap" id="person_id">
                            <img className="detail-icon" title= "کد ملی" src={ID}/>
                            <div className= "ml-sm-3">{this.state.person.person_id}</div>
                        </div>
                        <div className="detail-item flex-sm-row flex-column flex-nowrap" title= "سابقه پزشکی" id="medical_info">
                            <img className="detail-icon" src={doctor}/>
                            <div className= "ml-sm-3">{en2fa[String(this.state.person.medical_info).toLowerCase()]}</div>
                        </div>
                        <button id= "edit-btn" className= "btn align-self-center mt-3" onClick= {() => {this.setState({editInfoOpen: true})}}>
                            ویرایش
                        </button>
                        
                    </div>
                    <EditInfo 
                        isOpen={this.state.editInfoOpen} onClose={() => {this.setState({editInfoOpen: false})}} reloadData={this.fetchUserData}
                        person={this.state.person} key={this.state.person.person_id} 
                    />
                </div>
   
        );
    }
}