import React from 'react';

import 'bootstrap/dist/css/bootstrap.css'
import './GameStage.scss';

import BootstrapSwitchButton from 'bootstrap-switch-button-react'
import axios from 'axios';
import {NotificationManager} from 'react-notifications';
import {baseUrl, gameStageSendDataAPI} from '../../../../../APIData';
import {CountDown} from '../../../../CountDown';
import SwitchOptionWrapper from '../SwitchOptionWrapper';

export default class GameStage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            seconds: this.props.duration,
            autoMode: true,
        }

        this.options = {
            car: true,
            game: true, 
            system: true,
        }
    }
    
    toggleAutomationState = (checked) => {
        this.setState({autoMode: checked});
    }

    sendOptions = () => {
        return axios.post(baseUrl + gameStageSendDataAPI, this.options, {
            headers: {token: localStorage.getItem("token")},
        })
        .then(res => {
            return true;
        })
        .catch(error => {
            if (typeof error.response == "undefined") {
                ((message) => {NotificationManager.error(message);})('ارتباط با سرور برقرار نشد');
            }

            else ((message) => {NotificationManager.error(message);})(error.response.data.detail);
            return false;
        });
    }

    finishStage = async () => {
        if (await this.sendOptions())
            this.props.notifyEnd();
    }

    render() {
        return (
            <div className= "d-flex flex-column align-items-center mt-2">
                <div id= "timer" className= "mt-5">
                    <CountDown key= {String(!this.props.isCurrentStage || !this.state.autoMode) + String(this.props.duration)} 
                        duration= {this.props.duration}
                        halt= {!this.props.isCurrentStage || (this.state.autoMode == false)}
                        notifyCountEnd= {this.finishStage} />
                </div>

                <SwitchOptionWrapper title= "تغییر مرحله">
                        <BootstrapSwitchButton
                            checked={true}
                            onlabel='خودکار'
                            onstyle='success'
                            offlabel='دستی'
                            offstyle='info'
                            style='w-100 mx-3'
                            onChange={(checked) => {this.toggleAutomationState(checked);}}
                        />
                </SwitchOptionWrapper>

                <SwitchOptionWrapper title= "سیستم خبره">
                        <BootstrapSwitchButton
                            checked={true}
                            onlabel='فعال'
                            onstyle='success'
                            offlabel='غیر فعال'
                            offstyle='light'
                            style='w-100 mx-3'
                            onChange={(checked) => {
                                this.options.system = checked;
                            }}
                        />
                </SwitchOptionWrapper>

                <SwitchOptionWrapper title= "ماشین">
                        <BootstrapSwitchButton
                            checked={true}
                            onlabel='فعال'
                            onstyle='success'
                            offlabel='غیر فعال'
                            offstyle='light'
                            style='w-100 mx-3'
                            onChange={(checked) => {
                                this.options.car = checked;
                            }}
                        />
                </SwitchOptionWrapper>

                <SwitchOptionWrapper title= "بازی">
                        <BootstrapSwitchButton
                            checked={true}
                            onlabel='فعال'
                            onstyle='success'
                            offlabel='غیر فعال'
                            offstyle='light'
                            style='w-100 mx-3'
                            onChange={(checked) => {
                                this.options.game = checked;
                            }}
                        />
                </SwitchOptionWrapper>

                <div className= "mt-5">
                    <button className= {`button btn btn-primary ${this.state.autoMode == null ? " invisible" :
                        this.state.autoMode ? " hidden" : " visible"}`} onClick= {this.finishStage}>مرحله بعد</button>
                </div>
            </div>
        );
    }
}