import React from 'react';

import 'bootstrap/dist/css/bootstrap.css'
import './WheelStage.scss';

import BootstrapSwitchButton from 'bootstrap-switch-button-react'
import axios from 'axios';
import {NotificationManager} from 'react-notifications';
import {baseUrl, performWheelAPI} from '../../../../../APIData';
import {CountDown} from '../../../../CountDown';
import SwitchOptionWrapper from '../SwitchOptionWrapper';

export default class WheelStage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            autoMode: null,
            wheelAutoMode: null,
        }
        this.wheelManualStatus = true;
    }
    

    sendWheelStatus = (wheelStatus) => {
        axios.post(baseUrl + performWheelAPI, {status: wheelStatus}, {
            headers: {token: localStorage.getItem("token")},
        })
        .then(res => {
            ((message) => {NotificationManager.success(message);})('وضعیت چرخ تغییر کرد');
        })
        .catch(error => {
            if (typeof error.response == "undefined") {
                ((message) => {NotificationManager.error(message);})('ارتباط با سرور برقرار نشد');
            }
            else ((message) => {NotificationManager.error(message);})(error.response.data.detail);
        });
    }


    render() {
        return (
            <div className= "d-flex flex-column align-items-center mt-2">

                <div id= "timer" className= "mt-5">
                    <CountDown key= {String(!this.props.isCurrentStage || !this.state.autoMode) + String(this.props.duration)} 
                        duration= {this.props.duration}
                        halt= {!this.props.isCurrentStage || (this.state.autoMode == false)}
                        notifyCountEnd= {this.props.notifyEnd} />
                </div>
                <SwitchOptionWrapper title= "تغییر مرحله">
                        <BootstrapSwitchButton
                            checked={true}
                            onlabel='خودکار'
                            onstyle='success'
                            offlabel='دستی'
                            offstyle='info'
                            style='w-100 mx-3'
                            onChange={(checked) => {
                                this.setState({autoMode: checked});
                            }}
                        />
                </SwitchOptionWrapper>

                <SwitchOptionWrapper title= "تنظیم چرخ نوری">
                        <BootstrapSwitchButton
                            checked={true}
                            onlabel='خودکار'
                            onstyle='success'
                            offlabel='دستی'
                            offstyle='info'
                            style='w-100 mx-3'
                            onChange={(checked) => {
                                this.setState({wheelAutoMode: checked});
                                if (checked) 
                                    this.sendWheelStatus("auto");
                                else this.sendWheelStatus(this.wheelManualStatus);
                            }}
                        />
                </SwitchOptionWrapper>

                <div className= {` ${this.state.wheelAutoMode == null ? " invisible" : this.state.wheelAutoMode ? " hidden" : " visible"}`}>
                    <SwitchOptionWrapper title= "وضعیت چرخ نوری">
                        <BootstrapSwitchButton
                            checked={this.wheelManualStatus}
                            onlabel='روشن'
                            onstyle='success'
                            offlabel='خاموش'
                            offstyle='light'
                            style='w-100 mx-3'
                            onChange={(checked) => {
                                this.wheelManualStatus = checked;
                                this.sendWheelStatus(checked);
                            }}
                        />
                    </SwitchOptionWrapper>
                </div>
                <div className= "mt-5">
                    <button className= {`button btn btn-primary ${this.state.autoMode == null ? " invisible" :
                        this.state.autoMode ? " hidden" : " visible"}`} onClick= {this.props.notifyEnd}>مرحله بعد</button>
                </div>
            </div>
        );
    }
}