

import React from 'react';
import {Login} from '../Login';
import {Control} from '../Control';
import axios from 'axios';
import { Layout } from '../Layout';

import {baseUrl, verifyTokenAPI} from '../../APIData';

class Index extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loggedIn: false,
            loading: true,
        }
    }

    componentDidMount() {
        const token = localStorage.getItem("token") ;
        if (token != null) {
            axios.get(baseUrl + verifyTokenAPI, {
                headers: {token: token}
            })
            .then(res => {
                if (res.data.login_status)
                    this.state.loggedIn = true;
                else this.state.loggedIn = false;
            })
            .catch(err => {
                if (err.reponse.status == 400)
                    localStorage.removeItem("token");
                this.state.loggedIn = false;
            })
            .then(() => {
                this.state.loading = false;
                this.setState(this.state);
            })
        }
        else this.setState({loggedIn: false, loading: false});
    }

    render() {
        const componentToMount= this.state.loading ? [<Layout loading= {true}></Layout>]
            : this.state.loggedIn ? [<Control/>] : [<Login/>];
        return componentToMount
    }
}
