import React, { Component } from 'react';
import {BrowserRouter, Route, Switch, Redirect} from "react-router-dom";
import Index from './components/Index';
import {Login} from './components/Login';
import {SignUp} from './components/SignUp';
import {Control} from './components/Control';
import Test from './Test';
class App extends Component {

  render() {
    return (
      <>
        <BrowserRouter>
          <>
            <Route path='/' exact={true} render={() => <Control/>} />
            <Route path='/login' exact={true} render={() => <Login/>} />
            <Route path='/signup' exact={true} render={() => <SignUp/>} />
            <Route path='/control' exact={true} render={() => <Control/>} />
            <Route path='/temp' exact={true} render={() => <Test/>} />
          </>
        </BrowserRouter>
      </>
    );
  }
}

export default App;

