import React from 'react';

import 'bootstrap/dist/css/bootstrap.css'
import './Stage.scss';

export default class Stage extends React.Component {
    constructor(props) {
        super(props);
    }
    
    render() {
        return (
            <div id= "stage" className= {`${this.props.isCurrentStage ? " showup"
                : this.props.dead ? " goodby" : "standby"}`}>
                {this.props.children}
            </div>
        );
    }
}